* {
  box-sizing: border-box;
}

body {
  padding: 0;
  margin: 0;
  width: 100%;
  height: 100vh;
}

#root {
  height: 100%;
}

.App {
  font-family: sans-serif;
  text-align: center;

  display: grid;
  place-items: center;
  width: 100%;
  height: 100%;
}

svg path {
  fill: red;
}